import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { gradedExercises } from 'dump/data';

const columns = [
  {
    title: 'Xếp loại rèn luyện',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Xếp loại rèn luyện',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Tên tiếng anh',
    dataIndex: 'nameEn',
    key: 'nameEn',
  },
  {
    title: 'Từ điểm',
    dataIndex: 'fromScores',
    key: 'fromScores',
  },
  {
    title: 'Đến điểm',
    dataIndex: 'toScores',
    key: 'toScores',
  },
  {
    title: 'Điểm cộng tháng 10',
    dataIndex: 'plusMark10',
    key: 'plusMark10',
  },
  {
    title: 'Điểm cộng tháng 4',
    dataIndex: 'plusMark4',
    key: 'plusMark4',
  },
  {
    title: 'Operation',
    dataIndex: 'operation',
    render: (text, record) =>
      gradedExercises.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#/converted/edit/${record.code}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class ConvertedTrainingPointsPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Quy đổi điểm rèn luyện">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/converted/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={gradedExercises} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ConvertedTrainingPointsPage />
  </Page>
);



